.create {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
}

.create label span {
  display: block;
  margin: 30px 0 10px;
}

.create p {
  margin-top: 10px;
  font-size: 0.8em;
}

.create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color:  rgba(5, 63, 54, 0.506);
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
  
}

.ingredients {
  display: flex;
  align-items: center;
}

.ingredients button {
  margin: 0 0 0 10px;
}

.create.dark h2,
.create.dark span,
.create.dark li,
.create.dark p {
  color: #e4e4e4;
}

.create.dark button {
  background-color: #888;
}